.post {
  display: flex;
  flex-direction: column;
  gap: 16px;
  background-color: var(--bg-surface);
  padding: 24px;
  border-bottom: 2px solid var(--border-soft);
}

.profileinfo {
  display: flex;
  gap: 12px;
  color: var(--text-primary);
  align-items: center;
}

.profilephoto {
  border-radius: 50%;
  width: 48px;
  height: 48px;
}

.secondary_text {
  font: 400 16px Inter;
  color: var(--text-secondary);
  height: 24px;
}

.primary_text {
  font: 500 16px Inter;
  color: var(--text-primary);
  height: 24px;
}

.posttext {
  font-weight: 400;
  font-size: 20px;
}

.postContent img {
  max-width: 100%;
}

.interactiveboard {
  display: flex;
  gap: 16px;
}

.likes,
.comments {
  display: flex;
  gap: 8px;
  align-items: center;
}

.commentDropdown {
  display: flex;
  gap: 8px;
}

.commentsList {
  list-style-type: none;
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.commentsListBlock {
  display: flex;
  justify-content: space-between;
  align-items: center;
  animation: commentAppear 0.4s ease-out forwards;
}

@keyframes commentAppear {
  from {
    opacity: 0;
    transform: translateY(12px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}